* {
  padding: 0;
  margin: 0;
  text-decoration: none;
}

body {
  background-color: #ebeff0;
  color: #3b464a;
  font-family: "Share Tech Mono", monospace;
}

.portada {
  display: flex;
  justify-content: center;
  padding: 1rem;
  text-align: center;
}

.portada > img {
  margin: 0 2rem;
}

.portada > div {
  text-align: center;
}

.portada > div > h1 {
  background-image: url(../public/img/envolver.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 500px 410px;
  padding: 6rem;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

nav {
  /* border: 0.2rem white solid; */
  /* margin: 1rem; */
  /* border-radius: 1rem; */
  padding: 0.1rem;
  background-color: #397887;
}

nav > ul {
  display: flex;
  justify-content: space-around;
}

nav > ul > li {
  list-style: none;
  /* border: 0.2rem white solid; */
  padding: 1rem;
  border-radius: 1rem;
}

nav > ul > li > a {
  color: #3b464a;
  text-transform: uppercase;
  font-weight: bold;
}

