#noticias {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 1rem auto;
}

.noticia {
  width: 20%;
  margin: 10px;
  /* height: 200%; */
  background-color: #397887;
  border: 1px solid #3b464a;
  border-radius: 15px;
}

#noticias > .noticia > img {
  object-fit: cover;
  width: 100%;
  height: 300px;
  margin-bottom: 1rem;
  border-radius: 15px 15px 0px 0px;
}

#noticias > .noticia > header {
  padding-left: 1rem;
  padding-right: 1rem;
}

#noticias > .noticia > header > h3 {
  color: rgb(245, 178, 64);
  margin-bottom: 0.5rem;
}

#noticias > .noticia > header > p {
  margin-bottom: 1rem;
}



#comentarios {
  /* border: 2px solid white; */
  margin: auto;
  padding: 1rem;
  width: 80vw;
  text-align: center;
}
#comentarios > article {
  display: flex;
  margin: 1rem 1.5rem;
  /* border-top: 2px solid white; */
  border-bottom: 1px solid #758e97;
  padding: 1rem;
}

#comentarios > article > img {
  border-radius: 100%;
  width: 75px;
  height: 75px;
}

.titulo {
  display: flex;
  margin-bottom: 1rem;
}

.titulo > h4 {
  margin-right: 1rem;
}

#comentarios > article > div {
  margin-left: 1rem;
  text-align: start;
}

#tu_comentario {
  display: flex;
  width: 50vw;
  text-align: center;
  margin: auto;
}

#tu_comentario > div {
  padding: 1rem;
  margin: 1rem auto;
}

input,
label,
textarea {
  padding: 1rem;
  width: 40vw;
  display: block;
  margin: 10px;
  background-color: #ebeff0;
  border: 1px #758e97 solid;
  font-family: "Share Tech Mono", monospace;
}

button {
  padding: 1rem;
  border-radius: 30px;
  background-color: rgb(255, 184, 60);
  font-family: "Share Tech Mono", monospace;
  font-weight: bold;
}

h2 {
  text-align: center;
  text-transform: uppercase;
}

aside {
  border: 3px solid gold;
  position: absolute;
  top: 0px;
  right: 220px;
  z-index: 2;
  padding: 1rem 1rem 0rem 1rem;
  border-radius: 15px;
  color: rgb(255, 255, 255);
  text-align: center;
  background-color: rgb(7, 45, 116);
}

aside>h4{
  color: gold;
}

small {
  margin-top: 0.2rem;
  font-size: 12px;
  color: silver;
}

footer {
  text-align: center;
  background-color: rgb(59, 59, 59);
  color: #397887;
  padding: 1.2rem;
}

